<template>
    <div id="wrapper">
        <h1>{{auction.title}}</h1>
        <h2>
            <Countdown :date="auction.publishedOn"></Countdown>
        </h2>
        <img id="auctionPic" src="@/assets/pictures/auction.jpg" />
        <p>{{auction.description}}</p>
        <div class="formInput">
            <input v-model="amount" step="0.01" min="0" type="number" id="usermsg" />
            <div class="btn btn-submit" id="submitmsg" type="submit" value="Bid" v-on:click="placeBid">Bid</div>
        </div>
    </div>
</template>

<script>
    import Countdown from '@/components/Countdown.vue';
    import Auction from '@/models/auction.js'
    import AuctionService from '@/services/auctionService.js'

    export default {
        components: { Countdown },
        data() {
            return {
                auction: null,
                amount: 0
            }
        },
        async created() {
            var result = await AuctionService.getById(this.$route.params.id);
            this.auction = new Auction(result.auction.id, result.auction.title, result.auction.description, result.auction.publishedOn);
        },
        methods: {
            placeBid() {
                console.log(this.amount);
                AuctionService.placeBid(this.auction.id, this.amount);
            }
        }
    }
</script>
<style>
    #auctionPic{
        width: 80%;
        padding: 10px;
    }
    #submitmsg {
        background: #ff9800;
        border: 2px solid #e65100;
        color: white;
        padding: 4px 10px;
        font-weight: bold;
        border-radius: 4px;
        cursor: pointer !important;
        width: 20%;
        margin-left: 10px;
    }

    #usermsg {
        flex: 1;
        border-radius: 4px;
        border: 1px solid #ff9800;
        padding-left: 5px;
    }
    #wrapper {
        margin: 20px auto;   
        background: #eee;
        width: 50%;
        max-width: 100%;
        border: 2px solid #212121;
        border-radius: 4px;
        padding: 20px;
    }
</style>
